				"html": "<p>あるものはある。ないものはない。</p>\n<p>果たしてそうか。ないものをないと本当に言えるのだろうか。</p>\n<br>\n<p>* * * * * * * * * *</p>\n<br>\n<p>対象領域を限定した場合には言えるだろう。</p>\n<p>目の前のテーブルの上にリンゴがあるのかないのか。\n答えは何れかであり，どちらもありえる。\nそしてある時点では決まっている。</p>\n<p>たとえ何者も観測していなかったとしても。</p>\n<br>\n<p>* * * * * * * * * *</p>\n<br>\n<p>対象領域を限定しない場合はどうか。\n人の認知しうる全てを対象とした場合。</p>\n<p>世界のどこかにリンゴはあるか。</p>\n<p>あるだろう。</p>\n<p>たとえ過去または将来のある時点でリンゴのない時代があったとしても，時点を限定しなければ，やはりあるといえる。\n我々は少なくともある時点ではそれがあったことを知っているからだ。</p>\n<br>\n<p>実在が不確実のものであっても「ない」としようとしているものの概念はある。</p>\n<p>“宇宙に終わりはあるのか”などといった場合，その是非はともかく，“宇宙の終わり”という概念は自体は存在する。</p>\n<p>空想の産物であっても，やはり頭の中で概念としては生み出されている。\nそしてその概念を共有した時，はじめて「ある」のか「ない」のかを含めて論ずることができるようになる。</p>\n<p>また，対象領域を限定しないということは，無限の可能性を含む並行宇宙のように実在の有無についても言及はしきれない。</p>\n<br>\n<p>* * * * * * * * * *</p>\n<br>\n<p>概念というものの集合について考えてみると，概念のないものは含まれない。</p>\n<p>「ない」としようとしているものの対象は概念的には「ある」ので含まれる。</p>\n<p>「ない」という概念についても，それ自体がこの問そのものではあるが，含まれる。</p>\n<p>「ある」という概念については「ない」がないなら全て「ある」になるので，あえて定義する必要のある概念ではなくなり「ない」となってしまうのか。\nいや，しかしながら概念の集合には「ある」も「ない」も含まれるだろう。</p>\n<br>\n<p>では，概念の集合に含まれないものは何か。</p>\n<p>概念は人の知識を抽象化したものとすると，概念の集合は人の知識の集合に含まれる。</p>\n<p>　　概念 ⊆ 人の知識</p>\n<p>つまり，少なくとも誰も知らないものは概念の集合には含まれない。</p>\n<br>\n<p>人は知らないものは「ある」と知っている。</p>\n<p>知らなかったものを知るという経験を繰り返すことで，まだ他にも知らないことはあると類推する。\nあるいは，ある人は知っているが別のある人は知らないという状況をみることによって。\nまた，存在は知っているが，その本質は理解できないという事柄によっても。\n実際，すべてを知っている人はいないだろう。</p>\n<p>“知らないもの”はあるといったときでも，“知らないもの”という概念は存在する。\nしかし，その具体的な中身については知らないので言及はできない。</p>\n<br>\n<p>* * * * * * * * * *</p>\n<br>\n<p>暗黒物質（ダークマター）や暗黒エネルギー（ダークエネルギー）のような，それが存在することは分かるが，それが何かは分からないというものもある。\n我々の住む宇宙の大部分を占めるにも関わらず，それが何なのか分からないというのは興味深い。\n分かっていることは，いくつかの特徴と「何ではないか」ということ。</p>\n<p>これは重要な示唆を含んでいる。\nそのものをより注意深く見ようと思ったら，そのものだけを見ようとするのではなく，そのものの輪郭を見ることも大切である。</p>\n<p>これは逆も言える。「ない」ものを知ろうと思ったら，「ある」ものについてより深く知ることも大切である。</p>\n<p>我々は何を得ているのか，何を知っているのか。</p>\n<p>知っていると思っていることも案外と知らないこともあるし，知らないと思っていたことも実は心得ていた範疇かもしれない。\nその輪郭を意識してみることで，よくよく見つめ直すことにつながるかもしれない。</p>",
